.line{
  background-color: #f54932;
  height: 60px;
  &__title{
    padding: 18px 0;
    color: #ffffff;
    font-family: Merriweather;
    font-size: 22px;
    font-weight: 400;
    line-height: 110%;
    letter-spacing: 0px;
    text-transform: uppercase;
    @include mobile-md{
      font-size: 16px;
      font-weight: 400;
      line-height: 110%;
    }
  }
  &__point{
    padding: 9px 12px;
    @include mobile-md{
      padding: 10px 0; 
    }
  }
}