.stage{
  
  &__title{
    color: #313131;
    font-family: Merriweather;
    font-size: 54px;
    font-weight: 400;
    line-height: 120%;
    text-transform: uppercase;
    position: absolute;
    @include mobile-md{
      position: static;
      font-size: 36px;
      font-weight: 400;
      line-height: 110%;
      margin-bottom: 12px;
    }
  }
  &__descr{
    color: #3057a2;
    font-family: GolosText;
    font-size: 20px;
    font-weight: 400;
    line-height: 120%;
    padding-top: 70px;
    padding-left: 300px;
    margin-bottom: 80px;
    @include mobile-md{
      padding: 0;
    }
  }
  &__cards{
    gap: 20px;
    margin-bottom: 220px;
  }
  &__card{
    background-image: url(../img/bg-card.png);
    position: relative;
    &-3{
      grid-template-areas: 1 / 2 span;
    }
    &-4{
      grid-column:  1/1 span;
    }
    &-6{
      grid-column: 1/1 span;
    }
    &-7{
      grid-column: 2/3 span;
      background-image: url(../img/planer.png);
      background-repeat: no-repeat;
      background-position: 100px 100px;
      
    }
  }
  &__num{
    position: absolute;
    left: 15px;
    top: 50px;
    color: #313131;
    font-family: Golos Text;
    font-size: 20px;
    font-weight: 600;
    line-height: 120%;
    letter-spacing: 0px;
    padding: 11px 13px;
    background-color: #fff;
    border-radius: 60px;
    
  }
  &__desc{
    padding: 20px 20px 20px 70px;
    color: #313131;
    font-family: GolosText;
    font-size: 20px;
    font-weight: 500;
    line-height: 120%;
    width: 350px;
    &-7{
      width: 446px;
    }
  }
}