.hero{
  background-image: url(../img/yandex-hero.png);
  @include mobile-md{
    background-position: 0 -70px;
    background-repeat: no-repeat;
    background-image: url(../img/md_bg.png);
  }
  
  &__title{
    color: #313131;
    font-family: Merriweather;
    font-size: 60px;
    font-weight: 400;
    line-height: 110%;
    letter-spacing: 0px;
    width: 542px;
    padding-top: 100px;
    text-transform: uppercase;
    @include mobile-md{
      font-size: 36px;
      font-weight: 400;
      line-height: 110%;
      padding-top: 90px;
    }
  }
  &__title--center{
    padding: 0;
    margin-left: 91px;
    @include mobile-md{
      font-size: 36px;
      font-weight: 400;
      line-height: 110%;
    }
  }
  &__title--down{
    padding: 0;
    margin-left: 38px;
    margin-bottom: 24px;
    @include mobile-md{
      font-size: 36px;
      font-weight: 400;
      line-height: 110%;
    }
  }
  &__text{
    width: 602px;
    justify-content: center;
    margin-bottom: 40px;
    @include mobile-md{
      display: block;
      margin-bottom: 32px;
    }
  }
  &__descr{
    color: #313131;
    font-family: GolosText;
    font-size: 18px;
    font-weight: 400;
    line-height: 130%;
    letter-spacing: 0px;
    width: 542px;
    text-indent: 20px;
    &--md{
      visibility: hidden;
      width: 0;
      height: 0;
      padding: 0;
      margin: 0;
    }
    @include mobile-md{
      visibility: hidden;
      width: 0;
      height: 0;
      padding: 0;
      margin: 0;
      font-size: 18px;
      font-weight: 400;
      line-height: 130%;
      letter-spacing: 0px;
      max-width: 335px;
      text-indent: 0;
      &--md{
        visibility: visible;
        width: 100%;
        height: 100%;
      }
    }
  }
  &__btn{
    gap: 16px;
    padding-bottom: 140px;
    @include mobile-md{
      display: block;
      padding-bottom: 210px;
    }
  }
  &__btn--black{
    &-md{
      visibility: hidden;
      width: 0;
      padding: 0;
      height: 0;
      margin: 0;
    }
    color: #ffffff;
    font-family: GolosText;
    font-size: 16px;
    font-weight: 500;
    line-height: 130%;
    letter-spacing: 0px;
    background-color: #1f1f1f;
    width: 263px;
    @include hover{
      background-color: #fbce51;
      color: #1f1f1f;
    }
    @include mobile-md{
      &-md{
        visibility: visible;
        width: 100%;
        height: 100%;
        padding: 20px;
        margin-bottom: 14px;
        min-width: 335px;
        display: block;
      }
      display: none;
      visibility: hidden;
      width: 0;
      padding: 0;
      height: 0;
      margin: 0;
    }
  }
  &__btn--white{
    color: #1f1f1f;
    font-family: GolosText;
    font-size: 16px;
    font-weight: 500;
    line-height: 130%;
    letter-spacing: 0px;
    border: 2px solid #1f1f1f;
    width: 263px;
    @include hover{
      background-color: #1f1f1f;
      color: #fff;
    }
    @include  mobile-md{
      background-color: #ffffff;
      border: 2px solid #ffffff;
      padding: 16px 20px;
      min-width: 335px;
    }
  }
}