.tourney{
  padding: 106px 0 200px;
  @include mobile-md{
    padding: 48px 0;
  }
  &__title{
    font-family: Merriweather;
    font-size: 36px;
    font-weight: 700;
    line-height: 135%;
    letter-spacing: 0px;
    color: #313131;
    min-width: 764px;
    text-transform: uppercase;
    &--md{
      visibility: hidden;
      width: 0;
      padding: 0;
      height: 0;
      margin: 0;
    }
    @include mobile-md{
      display: none;
      &--md{
        visibility: visible;
        width: 335px;
        height: 152px;
        color: #313131;
        font-family: Merriweather;
        font-size: 28px;
        font-weight: 400;
        line-height: 135%;
        text-transform: uppercase;
      }
    }
  }
  &__image{
    @include mobile-md{
      margin-bottom: 24px;
    }
  }
  &__color{
    color: #f54932;
  }
  &__up{
    gap: 64px;
    margin-bottom: 91px;
    @include mobile-md{
      display: block;
    }
  }
  &__center{
    gap: 17px;
    @include mobile-md{
      display: block;
    }
  }
  &__subtitle{
    font-family: Merriweather;
    font-size: 36px;
    font-weight: 400;
    line-height: 135%;
    letter-spacing: 0px;
    text-transform: uppercase;
    color: #313131;
    width: 704px;
    margin-bottom: 60px;
    @include mobile-md{
      font-size: 28px;
      font-weight: 400;
      line-height: 135%;
      letter-spacing: 0px;
      width: 335px;
    }
  }
  &__text{
    @include mobile-md{
      display: none;
    }
    &--md{
      visibility: hidden;
      width: 0;
      margin: 0;
      height: 0;
      padding: 0;
      @include mobile-md{
        visibility: visible;
        width: 100%;
        height: 100%;
      }
    }
  }
  &__link{
    color: #3057a2;
    font-family: GolosText;
    font-size: 20px;
    font-weight: 400;
    line-height: 120%;
  }
  &__li{
    margin-bottom: 48px;
  }
  &__right{
    border-right: 2px solid #d0d0d0;
  }
  &__descr{
    padding: 16px 16px 16px 0;
    color: #313131;
    font-family: GolosText;
    font-size: 20px;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: 0px;
    opacity: 0.7;
    border-bottom: 2px solid #d0d0d0;
    &--last{
      border: none;
    }
    @include mobile-md{
      font-size: 20px;
      font-weight: 400;
      line-height: 120%;
      padding: 0;
      margin-bottom: 12px;
      border: none;
    }
  }
  &__desc{
    padding: 16px 0 16px 16px;
    color: #313131;
    font-family: GolosText;
    font-size: 20px;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: 0px;
    border-bottom: 2px solid #d0d0d0;
    &--last{
      border: none;
    }
    @include mobile-md{
      font-size: 20px;
      font-weight: 600;
      line-height: 120%;
      padding:  0 0 16px 0;
      margin-bottom: 16px;
    }
  }
  &__rub{
    padding-right: 12px;
    text-decoration: line-through 2px solid #f54932;
  }
}