.player{
  position: relative;
  &__title{
    color: #313131;
    font-family: Merriweather;
    font-size: 54px;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: 0px;
    text-align: left;
    text-transform: uppercase;
    margin-bottom: 80px;
    @include mobile-md{
      font-size: 36px;
      font-weight: 400;
      line-height: 110%;
      letter-spacing: 0px;
    }
  }
  
  &__navigation{
    position: absolute;
    top: 30px;
    right: 150px;
    transform: translate(-50%, -50%);
    z-index: 10;
    @include mobile-md{
      right: 200px;
      top: 650px;
    }
  }
  &__prev{
    min-width: 44px;
    min-height: 44px;
    padding: 20px 20px 14px 25px;
    background-color: #313131;
    border-radius: 100px;
    margin-left: 63px;
    &::after{
      content: url(../img/yandex__btn2.svg);
    }
    @include hover{
      background-color: #fbce51;
    }
  }
  &__next{
    min-width: 44px;
    min-height: 44px;
    padding: 14px 20px 25px 16px;
    background-color: #313131;
    border-radius: 100px;
    &::after{
      content: url(../img/yandex__btn1.svg);
    }
    @include hover{
      background-color: #fbce51;
    }
  }
  
  &__slide{
    flex-direction: column;
    width: 394px;
    margin-bottom: 140px;
  }
  &__image{
    width: 293px;
    margin-bottom: 20px;
  }
  &__descr{
    color: #313131;
    font-family: GolosText;
    font-size: 24px;
    font-weight: 600;
    line-height: 120%;
    margin-bottom: 6px;
  }
  &__desc{
    color: #313131;
    font-family: GolosText;
    font-size: 20px;
    font-weight: 400;
    line-height: 120%;
    opacity: 0.6;
    margin-bottom: 20px;
  }
  &__btn{
    padding: 12px;
    color: #3057a2;
    font-family: GolosText;
    font-size: 16px;
    font-weight: 500;
    line-height: 120%;
    border: 1px solid #3057a2;
    border-radius: 62px;
    @include hover{
      color: #ffffff;
      border: 1px solid #3057a2;
      background-color: #3057a2;
    }
  }
}